.basic-datagrid {
  width: 100%;
  border-collapse: collapse;
}

.basic-datagrid table, .basic-datagrid th, .basic-datagrid td {
  border: 1px solid #acacac;
}

.basic-datagrid td {
  height: 1.5em;
  padding-left: 1em;
}

.basic-datagrid th {
  height: 1.9995em;
}

.basic-datagrid tbody tr:hover {
  background-color: #ffd3d3;
}

.stripe-color {
  background-color: #eee;
}

.basic-datagrid-error {
  width: 100%;
  height: 3em;
  color: #000;
  background-color: #ffbfbf;
  border-radius: 10px;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding-left: 1.5em;
  font-size: 1.3em;
  display: flex;
}

.display-none {
  display: none;
}

.basictable-td {
  position: relative;
}

.popupcellinfo {
  width: 700px;
  z-index: 999;
  visibility: hidden;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 10px;
  padding: 3px;
  animation: 1s forwards fadeIn;
  position: absolute;
  top: 20px;
  left: 20px;
}

@keyframes fadeIn {
  99% {
    visibility: hidden;
  }

  100% {
    visibility: visible;
  }
}

.basictable-pagination {
  height: 2em;
  border-bottom: 1px solid #acacac;
  border-left: 1px solid #acacac;
  border-right: 1px solid #acacac;
}

.basictable-pagination-button {
  cursor: pointer;
  text-align: center;
  height: 100%;
  border: none;
  padding: 0 10px;
  text-decoration: none;
}

.basictable-pagination-action {
  background-color: #eee;
}

.basictable-pagination-action:hover, .basictable-pagination-number {
  background-color: #fff;
}

.basictable-pagination-selected {
  background-color: #ffd3d3;
}

.hide-row {
  display: none;
